import { format } from "date-fns";
import { T as TARGETED_ATTACKS, G as GLOBAL_ATTACK_SCENARIOS, a as BOARD_ITEMS, B as BOARD_SUPPLY_CHAINS, S as STAGES } from "./targeted-attacks.js";
import { produce } from "immer";
const getStage = (stageId) => STAGES.find((stage) => stage.id === stageId);
const getSharedGameContext = (context) => ({
  timestamp: context.timestamp,
  gameId: context.gameId,
  hostUserId: context.hostUserId,
  finishedAssigningSides: context.finishedAssigningSides,
  globalAttackScenario: context.globalAttackScenario,
  targetedAttacks: context.targetedAttacks,
  users: context.users,
  events: context.events,
  defense: context.defense,
  attack: context.attack
});
const getGameSummary = (context) => {
  return {
    // Make sure all additional data is stripped from the game context.
    context: produce(getSharedGameContext(context), (draft) => {
      draft.users.forEach((user, i) => {
        user.name = `Player ${i + 1}`;
      });
    }),
    TARGETED_ATTACKS,
    GLOBAL_ATTACK_SCENARIOS,
    BOARD_ITEMS,
    BOARD_SUPPLY_CHAINS
  };
};
const getGameSummaryFilename = (context) => {
  const date = new Date(context.timestamp);
  return `game-data-${format(date, "yyyy-LL-dd")}.json`;
};
export {
  getGameSummary as a,
  getGameSummaryFilename as b,
  getStage as c,
  getSharedGameContext as g
};
